import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';


@Component({
  	selector: 'page-lista',
	templateUrl: 'listadecompras.html'
})


export class ListaPage {


	listaDeCompras: Array<string> = new Array();
	listaSalvas: Array<string> = new Array();
	/*itens de supermercado*/
	arroz: boolean;
	feijao: boolean;
	oleo: boolean;
	acucar: boolean;
	cafe: boolean;
	leiteempo: boolean;
	sal: boolean;
	macarrao: boolean;
	extratoTomate: boolean;
	tempero: boolean;
	achocolatado: boolean;
	molhoTomate: boolean;
	farinhaTrigo: boolean;
	gelatina: boolean;
	biscoito: boolean;
	milho: boolean;
	username: string;
	aux: string
	
	  
	constructor(public alertCtrl: AlertController,
		private storage: Storage, public navCtrl: NavController) { }
	

  	doPrompt() {

		this.listaDeCompras.splice(0, this.listaDeCompras.length);

		if(this.arroz)
			this.listaDeCompras.push("arroz");		
		if(this.feijao)
			this.listaDeCompras.push("feijao");		
		if(this.oleo)
			this.listaDeCompras.push("oleo");
		if(this.acucar)
			this.listaDeCompras.push("açúcar");
		if(this.sal)
			this.listaDeCompras.push("sal");
		if(this.cafe)
			this.listaDeCompras.push("cafe");
		if(this.leiteempo)
			this.listaDeCompras.push("leite em pó");
		if(this.macarrao)
			this.listaDeCompras.push("macarrão");
		if(this.extratoTomate)
			this.listaDeCompras.push("extrato de tomate");
		if(this.tempero)
			this.listaDeCompras.push("tempero completo");
		if(this.achocolatado)
			this.listaDeCompras.push("Achocolatado em pó");
		if(this.molhoTomate)
			this.listaDeCompras.push("molho de tomate");
		if(this.farinhaTrigo)
			this.listaDeCompras.push("farinha de trigo");
		if(this.gelatina)
			this.listaDeCompras.push("gelatina");
		if(this.biscoito)
			this.listaDeCompras.push("biscoito");
		if(this.milho)
			this.listaDeCompras.push("milho verde");
		
		
		console.log(this.listaDeCompras);

    	let prompt = this.alertCtrl.create({
			title: 'Nova lista de compras',
			message: "Digite o nome da sua lista de compras",
			inputs: [{
				name: 'title',
				placeholder: 'Nome'
			}],
			buttons: [{
				text: 'Cancelar',
				handler: data => {
					console.log('Cancel clicked');
				}
			},
			{
				text: 'Salvar',
				handler: data => {
					console.log('Saved clicked, lista criada: '+ data.title);
					this.listaSalvas.push(data.title);
					this.storage.set(data.title, this.listaDeCompras.toString());
				}
			}
			]
		});
		
		prompt.present();
		
	}

	obterValor(){
		
		for(let i = 0; i < this.listaSalvas.length; i++){
			this.storage.get(this.listaSalvas[i]).then((val) => {
				console.log('Nome da lista é '+ this.listaSalvas[i]);
				console.log('Sua lista salva é', val);
				let alert = this.alertCtrl.create({
					title: 'teste '+ this.listaSalvas[i],
					subTitle: val,
					buttons: ['OK']
				});
				alert.present();
			});
		}
		
	}
	
	
}



